/* ------------------- */
:root {
	--animation-delay: 0;
	--duration: 2000ms;
	--iterations: 1;
}

.reveal, .reveal::after {
	-webkit-animation-delay: 0;
	        animation-delay: 0;
	animation-delay: 0;
	-webkit-animation-delay: var(--animation-delay, 2s);
	        animation-delay: var(--animation-delay, 2s);
	-webkit-animation-iteration-count: 1;
	        animation-iteration-count: 1;
	animation-iteration-count: 1;
	-webkit-animation-iteration-count: var(--iterations, 1);
	        animation-iteration-count: var(--iterations, 1);
	-webkit-animation-duration: 800ms;
	        animation-duration: 800ms;
	-webkit-animation-duration: 2000ms;
	        animation-duration: 2000ms;
	-webkit-animation-duration: var(--duration, 800ms);
	        animation-duration: var(--duration, 800ms);
	-webkit-animation-fill-mode: both;
	        animation-fill-mode: both;
	-webkit-animation-timing-function: cubic-bezier(0.0, 0.0, 0.2, 1);
	        animation-timing-function: cubic-bezier(0.0, 0.0, 0.2, 1);
}
 
.reveal {
	position: relative;
	font-size: inherit;
	-webkit-animation-name: text;
	        animation-name: text;
	color: #111;
	white-space: nowrap;
	cursor: default;
	text-transform: lowercase;
	
}
 
.reveal::after {
	content: " ";
	position: absolute;
	z-index: 999;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: #fff;
	-webkit-transform: scaleX(0);
	        transform: scaleX(0);
	-webkit-transform-origin: 0 50%;
	        transform-origin: 0 50%;
	pointer-events: none;
	-webkit-animation-name: revealer;
	        animation-name: revealer;
	}
 
@-webkit-keyframes text {

	from {-webkit-clip-path: inset(0 100% 0 0);clip-path: inset(0 100% 0 0);}

	to {-webkit-clip-path: inset(0 0 0 0);clip-path: inset(0 0 0 0);}
}
 
@keyframes text {

	from {-webkit-clip-path: inset(0 100% 0 0);clip-path: inset(0 100% 0 0);}

	to {-webkit-clip-path: inset(0 0 0 0);clip-path: inset(0 0 0 0);}
}
 
 
@-webkit-keyframes revealer {
	
	0%, 50% {-webkit-transform-origin: 0 50%;transform-origin: 0 50%;}

	60%, 100% {-webkit-transform-origin: 100% 50%;transform-origin: 100% 50%;}
 
	60% {-webkit-transform: scaleX(0);transform: scaleX(0);}

	100% {-webkit-transform: scaleX(0);transform: scaleX(0);}
}
 
 
@keyframes revealer {
	
	0%, 50% {-webkit-transform-origin: 0 50%;transform-origin: 0 50%;}

	60%, 100% {-webkit-transform-origin: 100% 50%;transform-origin: 100% 50%;}
 
	60% {-webkit-transform: scaleX(0);transform: scaleX(0);}

	100% {-webkit-transform: scaleX(0);transform: scaleX(0);}
}


